import React from "react";
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  TableContainer,
  Table,
  TableBody,
  Paper,
  Typography,
} from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

import HistogramDisplay from "./HistogramDisplay";
import DateDisplay from "./DateDisplay";
import DepGraph from "./DepGraph";

const CustomAccordion = ({
  datum,
  summaryTitle,
  detailsData,
  renderListItemRow,
  variant,
  link_to_image,
  link_to_table_date_image,
  table_name,
  table_source,
  link_to_dep_graph,
}) => {
  const handleAccordionChange = (e) => {
    e.preventDefault();
  };

  return (
    <Accordion defaultExpanded onChange={handleAccordionChange}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel1a-content"
        id="panel1a-header"
      >
        <Typography variant={variant} sx={{ width: "85%", flexShrink: 0 }}>
          {summaryTitle}
        </Typography>
      </AccordionSummary>
      <AccordionDetails>
        {link_to_image ? HistogramDisplay(link_to_image) : null}
        {link_to_dep_graph ? (
          <DepGraph link_to_dep_graph={link_to_dep_graph}></DepGraph>
        ) : null}

        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableBody>
              {detailsData.map((col, i) => {
                const fieldName = col["field_name"];
                const fieldLabel = col["field_label"];
                if (datum[fieldName] != null) {
                  return renderListItemRow(datum[fieldName], fieldLabel, i);
                } else {
                  return null;
                }
              })}
            </TableBody>
          </Table>
        </TableContainer>
        {link_to_table_date_image
          ? DateDisplay(link_to_table_date_image, table_name, table_source)
          : null}
      </AccordionDetails>
    </Accordion>
  );
};

export default CustomAccordion;
