import React from "react";

import FilterTableModal from "./FilterTableModal";

function FilterTableModals({ categoricalColumns }) {
  return categoricalColumns.map((exampleCategoricalColumn) => {
    const {
      categoricalColumn,
      categoriesAndCounts,
      categoricalColumnLabel,
      categoricalColumnPlural,
    } = exampleCategoricalColumn;
    const categoricalColumnDisplay = categoricalColumnLabel;

    return (
      <FilterTableModal
        categoricalColumn={categoricalColumn}
        categoricalColumnDisplay={categoricalColumnDisplay}
        categoriesAndCounts={categoriesAndCounts}
        categoricalColumnPlural={categoricalColumnPlural}
        key={categoricalColumn}
      ></FilterTableModal>
    );
  });
}

export default FilterTableModals;
