import * as React from "react";

import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";

import OverallSearch from "./OverallSearch";
import AdvancedSearch from "./AdvancedSearch";

import { useSelector } from "react-redux";

export default function BasicCard({
  fieldsToSearchWithOverallSearchWithLabels,
  fieldsToSearchWithPrecisionSearchWithLabels,
}) {
  const advancedSearchOn = useSelector((state) => state.advancedSearchOn);
  const fieldsToSearchWithAdvancedSearchWithLabels =
    fieldsToSearchWithPrecisionSearchWithLabels;
  return (
    <Card>
      <CardContent>
        {advancedSearchOn ? (
          <AdvancedSearch
            fieldsToSearchWithAdvancedSearchWithLabels={
              fieldsToSearchWithAdvancedSearchWithLabels
            }
          ></AdvancedSearch>
        ) : (
          <OverallSearch
            fieldsToSearchWithOverallSearchWithLabels={
              fieldsToSearchWithOverallSearchWithLabels
            }
          ></OverallSearch>
        )}
      </CardContent>
    </Card>
  );
}
