import React, { Fragment } from "react";

import Modal from "@mui/material/Modal";
import Box from "@mui/material/Box";

import Typography from "@mui/material/Typography";
import Table from "@mui/material/Table";

import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";

import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

import LinksToScripts from "./LinksToScripts";
import LinkToData from "./LinkToData";

import HttpsLink from "./Helpers/HttpsLink";
import LinkFromMarkdown from "./Helpers/LinkFromMarkdown";
import LinkToEmail from "./Helpers/LinkToEmail";
import CustomAccordion from "./Helpers/CustomAccordion";
import SortableLookupsTables from "./SortableLookupsTable";
import DepGraph from "./Helpers/DepGraph";

import { stringIsMarkdownForHyperlink } from "../../../../Logic/stringIsMarkdownForHyperlink";
import { stringIsMarkdownForEmail } from "../../../../Logic/stringIsMarkdownForEmail";
import { stringIsHttpsLink } from "../../../../Logic/stringIsHttpsLink";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
  overflow: "auto",
  height: "75%",
  width: "75%",
  display: "block",
};

function AdditionalInfo({ open, handleClose, columnsOnAdditionalInfo, datum }) {
  // Render an individual row of the table
  const renderListItemRow = (datum, display, i) => {
    const stringIsHttps = stringIsHttpsLink(datum);
    const stringIsEmail = stringIsMarkdownForEmail(datum);
    const stringIsHyperlink = stringIsMarkdownForHyperlink(datum);

    if (stringIsHttps) {
      return HttpsLink(datum, display, i);
    }

    if (stringIsEmail) {
      return LinkToEmail(stringIsEmail, display, i);
    }

    if (stringIsHyperlink) {
      return LinkFromMarkdown(stringIsHyperlink, display, i);
    }

    // check to see if we need to convert new line breaks
    const mustFormat = String(datum).includes("\n");
    if (mustFormat) {
      const formattedDatum = String(datum).replace(/\n/g, "<br>");

      return (
        <TableRow
          key={i}
          sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
        >
          <TableCell>
            <b>{display}</b>
          </TableCell>

          <TableCell
            dangerouslySetInnerHTML={{ __html: formattedDatum }}
          ></TableCell>
        </TableRow>
      );
    } else {
    }

    return (
      <TableRow
        key={i}
        sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
      >
        <TableCell>
          <b>{display}</b>
        </TableCell>

        <TableCell>{datum}</TableCell>
      </TableRow>
    );
  };

  // Main rendering

  if (datum) {
    // Destructure Link to Image
    const { link_to_image } = datum;

    // Destructure Lookups Stuff
    const { merged_lookups } = datum;

    // Destructure link to dep_graph
    const { link_to_dep_graph } = datum;

    // Destructure stuff for Links to Data and Scripts
    const {
      table_name,
      table_link_csv,
      table_link_r,
      table_link_sas,
      table_link_stata,
      table_link_python,
      link_to_table_date_image,
      table_source,
    } = datum;

    const scriptForTableExists = Boolean(
      table_link_r || table_link_sas || table_link_stata || table_link_python
    );

    const tableLevelMetadataEntered =
      columnsOnAdditionalInfo["tableLevelEnteredWithLabels"];
    const tableLevelMetadataObserved =
      columnsOnAdditionalInfo["tableLevelObservedWithLabels"];
    const variableLevelMetadataEntered =
      columnsOnAdditionalInfo["variableLevelEnteredWithLabels"];
    const variableLevelMetadataObserved =
      columnsOnAdditionalInfo["variableLevelObservedWithLabels"];

    const tableLevelEnteredExists = tableLevelMetadataEntered.some((item) =>
      datum.hasOwnProperty(item.field_name)
    );
    const tableLevelObservedExists = tableLevelMetadataObserved.some((item) =>
      datum.hasOwnProperty(item.field_name)
    );
    const variableLevelEnteredExists = variableLevelMetadataEntered.some(
      (item) => datum.hasOwnProperty(item.field_name)
    );
    const variableLevelObservedExists = variableLevelMetadataObserved.some(
      (item) => datum.hasOwnProperty(item.field_name)
    );

    const renderTableLevelEntered = () => {
      return (
        <CustomAccordion
          datum={datum}
          summaryTitle="Entered into the Table Dictionary"
          detailsData={columnsOnAdditionalInfo["tableLevelEnteredWithLabels"]}
          columnsOnAdditionalInfo={columnsOnAdditionalInfo}
          renderListItemRow={renderListItemRow}
        />
      );
    };

    const renderTableLevelObserved = () => {
      return (
        <CustomAccordion
          datum={datum}
          summaryTitle="Observed in the Data"
          detailsData={columnsOnAdditionalInfo["tableLevelObservedWithLabels"]}
          columnsOnAdditionalInfo={columnsOnAdditionalInfo}
          renderListItemRow={renderListItemRow}
          link_to_table_date_image={link_to_table_date_image}
          table_name={table_name}
          table_source={table_source}
        />
      );
    };

    const renderVariableLevelEntered = () => {
      return (
        <CustomAccordion
          datum={datum}
          summaryTitle="Entered into the Data Dictionary"
          detailsData={
            columnsOnAdditionalInfo["variableLevelEnteredWithLabels"]
          }
          columnsOnAdditionalInfo={columnsOnAdditionalInfo}
          renderListItemRow={renderListItemRow}
          link_to_dep_graph={link_to_dep_graph}
        />
      );
    };

    const renderVariableLevelObserved = () => {
      return (
        <CustomAccordion
          datum={datum}
          summaryTitle="Observed in the Data"
          detailsData={
            columnsOnAdditionalInfo["variableLevelObservedWithLabels"]
          }
          renderListItemRow={renderListItemRow}
          link_to_image={link_to_image}
        />
      );
    };

    return (
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Stack spacing={2}>
            {/*
            --------------------------------------------------------------------
            Title
            --------------------------------------------------------------------
            */}
            <Box
              sx={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              <Typography id="modal-modal-title" variant="h5" component="h2">
                <Fragment>
                  Details for{" "}
                  <code>
                    {datum["table_source"]}::{datum["table_name"]}::
                    {datum["var_name"]}
                  </code>
                </Fragment>
              </Typography>

              {link_to_dep_graph ? (
                <DepGraph link_to_dep_graph={link_to_dep_graph}></DepGraph>
              ) : null}
            </Box>

            {/*
            --------------------------------------------------------------------
            Basic Metadata
            --------------------------------------------------------------------
            */}
            <Accordion>
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1b-content"
                id="panel1b-header"
              >
                <Typography variant="h6" sx={{ width: "85%", flexShrink: 0 }}>
                  Basic Metadata
                </Typography>
              </AccordionSummary>
              <AccordionDetails>
                <TableContainer component={Paper}>
                  <Table sx={{ minWidth: 650 }} aria-label="simple table">
                    <TableBody>
                      {columnsOnAdditionalInfo["basicWithLabels"].map(
                        (col, i) => {
                          const fieldName = col["field_name"];
                          const fieldLabel = col["field_label"];
                          if (datum[fieldName] != null) {
                            return renderListItemRow(
                              datum[fieldName],
                              fieldLabel,
                              i
                            );
                          } else {
                            return null;
                          }
                        }
                      )}
                    </TableBody>
                  </Table>
                </TableContainer>
              </AccordionDetails>
            </Accordion>
            {/*
            --------------------------------------------------------------------
            Additional Variable Level Metadata
            --------------------------------------------------------------------
            */}
            <Accordion>
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1b-content"
                id="panel1b-header"
              >
                <Typography variant="h6" sx={{ width: "85%", flexShrink: 0 }}>
                  Additional Variable Level Metadata
                </Typography>
              </AccordionSummary>
              <AccordionDetails>
                {variableLevelEnteredExists
                  ? renderVariableLevelEntered()
                  : null}

                {variableLevelObservedExists
                  ? renderVariableLevelObserved()
                  : null}
              </AccordionDetails>
            </Accordion>
            {/*
            --------------------------------------------------------------------
            Additional Table Level Metadata
            --------------------------------------------------------------------
            */}
            <Accordion>
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <Typography variant="h6" sx={{ width: "85%", flexShrink: 0 }}>
                  Additional Table Level Metadata
                </Typography>
              </AccordionSummary>
              <AccordionDetails>
                {tableLevelEnteredExists ? renderTableLevelEntered() : null}
                {tableLevelObservedExists ? renderTableLevelObserved() : null}
              </AccordionDetails>
            </Accordion>
            {/*
            --------------------------------------------------------------------
            Merged Lookups
            --------------------------------------------------------------------
            */}
            {/* 
            {merged_lookups ? (
              <Accordion>
                <AccordionSummary
                  expandIcon={<ExpandMoreIcon />}
                  aria-controls="panel1a-content"
                  id="panel1a-header"
                >
                  <Typography variant="h6" sx={{ width: "85%", flexShrink: 0 }}>
                    Lookups
                  </Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <MergedLookupsDisplay merged_lookups={merged_lookups} />
                </AccordionDetails>
              </Accordion>
            ) : null}
            */}
            {merged_lookups ? (
              <Accordion>
                <AccordionSummary
                  expandIcon={<ExpandMoreIcon />}
                  aria-controls="panel1a-content"
                  id="panel1a-header"
                >
                  <Typography variant="h6" sx={{ width: "85%", flexShrink: 0 }}>
                    Lookups
                  </Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <SortableLookupsTables
                    merged_lookups={merged_lookups}
                  ></SortableLookupsTables>
                </AccordionDetails>
              </Accordion>
            ) : null}
            {/*
            --------------------------------------------------------------------
            Link to Data
            --------------------------------------------------------------------
            */}
            {table_link_csv ? (
              <LinkToData
                table_name={table_name}
                table_link_csv={table_link_csv}
              ></LinkToData>
            ) : null}
            {/*
            --------------------------------------------------------------------
            Links to Scripts
            --------------------------------------------------------------------
            */}
            {scriptForTableExists ? (
              <LinksToScripts
                table_link_r={table_link_r}
                table_link_sas={table_link_sas}
                table_link_stata={table_link_stata}
                table_link_python={table_link_python}
              ></LinksToScripts>
            ) : null}
          </Stack>
        </Box>
      </Modal>
    );
  } else {
    return null;
  }
}

export default AdditionalInfo;
