import tlm from "./Data/tlm.json";
import vlm from "./Data/vlm.json";

// Create the rawData array
let rawData = vlm.map((v) => {
  // Find corresponding table level metadata
  let t = tlm.find(
    (t) => t.table_source === v.table_source && t.table_name === v.table_name
  );

  // If found, merge both variable and table level metadata
  if (t) {
    return { ...v, ...t };
  } else {
    return v;
  }
});

export { rawData };
