function search(field, term, data) {
  // This function searches through the 'data' array and returns an array of objects where the given field contains the given term

  return data.filter(
    (obj) =>
      obj.hasOwnProperty(field) &&
      obj[field].toLowerCase().includes(term.toLowerCase())
  );
}

function intersect(results1, results2) {
  // This function returns an array of objects that appear in both result arrays
  return results1.filter((obj1) =>
    results2.some((obj2) => obj1.uid === obj2.uid)
  );
}

function union(results1, results2) {
  // This function returns an array of objects that appear in either result array, removing duplicates
  let result = [...results1, ...results2];
  return result.filter(
    (obj, index, self) => index === self.findIndex((o) => o.uid === obj.uid)
  );
}

function processNode(node, data) {
  // This function handles the recursive processing of nodes
  if ("field" in node) {
    // base case: the node is a leaf node
    return search(node.field, node.term, data);
  } else {
    let leftResults = processNode(node.left, data);

    // if node.right and node.operator exist, perform the operation
    if (node.right && node.operator) {
      let rightResults = processNode(node.right, data);
      if (node.operator === "AND") {
        return intersect(leftResults, rightResults);
      } else if (node.operator === "OR") {
        return union(leftResults, rightResults);
      }
    }

    // if node.right and node.operator do not exist, return the leftResults
    return leftResults;
  }
}

export { processNode };
