import React, { Fragment } from "react";
import Typography from "@mui/material/Typography";

import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";

import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

function LinksToScripts({
  table_link_r,
  table_link_sas,
  table_link_stata,
  table_link_python,
}) {
  const displayLinkCard = (table_link, platform) => {
    const nameOfScript = table_link.substring(table_link.lastIndexOf("/") + 1);

    return (
      <Grid item xs={3}>
        <Card>
          <CardContent>
            <a href={`${table_link}`} download={`${nameOfScript}`}>
              {platform}
            </a>
          </CardContent>
        </Card>
      </Grid>
    );
  };

  return (
    <Fragment>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1b-content"
          id="panel1b-header"
        >
          <Typography variant="h6" sx={{ width: "85%", flexShrink: 0 }}>
            Code to Load and Label Parent CSV
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Grid container spacing={2}>
            {table_link_r ? displayLinkCard(table_link_r, "R") : null}
            {table_link_sas ? displayLinkCard(table_link_sas, "SAS") : null}
            {table_link_stata
              ? displayLinkCard(table_link_stata, "Stata")
              : null}
            {table_link_stata
              ? displayLinkCard(table_link_python, "Python")
              : null}
          </Grid>
        </AccordionDetails>
      </Accordion>
    </Fragment>
  );
}

export default LinksToScripts;
