const stringIsMarkdownForHyperlink = (string) => {
  const asString = String(string);

  const fullLinkOnlyRegex =
    /^\[([\w\s\d\(\)_%,-\-.+]+)\]\((https?:\/\/[\w\d./?=#:@,_&%\(\),\-+]+)\)$/;

  const fullLinkMatch = asString.match(fullLinkOnlyRegex);
  return fullLinkMatch;
};

export { stringIsMarkdownForHyperlink };
