import React, { Fragment } from "react";

import Grid from "@mui/material/Grid";
import { Stack } from "@mui/material";

import Typography from "@mui/material/Typography";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";

import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import Link from "@mui/material/Link";

function LinkToData({ table_name, table_link_csv }) {
  const nameOfDataFile = `${table_name}.csv`;
  const linkToDataFile = table_link_csv;

  return (
    <Accordion>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel1b-content"
        id="panel1b-header"
      >
        <Typography variant="h6" sx={{ width: "85%", flexShrink: 0 }}>
          Download the Parent CSV File
        </Typography>
      </AccordionSummary>
      <AccordionDetails>
        <Stack spacing={1}>
          <Typography variant="body">
            Permission to access the{" "}
            <Link href={"https://nih.sharepoint.com/sites/NIA-BLSA"}>
              BLSA Data Science SharePoint Online site
            </Link>{" "}
            is required.
          </Typography>

          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Card>
                <CardContent>
                  <a href={`${linkToDataFile}`} download={`${nameOfDataFile}`}>
                    {`${table_name}.csv`}
                  </a>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Stack>
      </AccordionDetails>
    </Accordion>
  );
}

export default LinkToData;
