import React, { Fragment } from "react";
import lucene from "lucene";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";

import {
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from "@mui/material";

import { useSelector, useDispatch } from "react-redux/es/exports";

import { processNode } from "../../Logic/processNodeForAdvancedSearch";

const operators = ["AND", "OR"];

function AdvancedSearch({ fieldsToSearchWithAdvancedSearchWithLabels }) {
  const fields = fieldsToSearchWithAdvancedSearchWithLabels.map(
    (d) => d.field_name
  );
  const fieldLabels = {};

  for (const field of fieldsToSearchWithAdvancedSearchWithLabels) {
    fieldLabels[field["field_name"]] = field["field_label"];
  }

  const [field, setField] = React.useState("");
  const [term, setTerm] = React.useState("");
  const [operator, setOperator] = React.useState("");
  const [query, setQuery] = React.useState("");

  const dispatch = useDispatch();

  const unfilteredData = useSelector((state) => state.unfilteredData);

  const handleClick = () => {
    dispatch({
      type: "SET_ADVANCED_SEARCH_ON",
      payload: false,
    });
    dispatch({
      type: "UPDATE_FILTERED_DATA",
    });
    dispatch({
      type: "SET_PAGE_NUMBER",
      payload: 1,
    });
  };

  const handleAddToQuery = () => {
    if (field && term) {
      // Format term to include double quotes if it consists of multiple words
      const formattedTerm = term.includes(" ") ? `"${term}"` : term;
      // Create new condition
      const newCondition = `${field}:${formattedTerm}`;
      // Determine operator string (removing 'Add with ' prefix and making uppercase)
      const operatorStr = operator ? operator.toUpperCase() : "";
      // Append new condition to query with operator, or set as initial condition if no existing query
      const newQuery = query
        ? `${query} ${operatorStr} ${newCondition}`
        : newCondition;
      // Update query and clear form inputs
      setQuery(newQuery);
      setField("");
      setTerm("");
      setOperator("");
    }
  };

  const handleSearch = () => {
    const ast = lucene.parse(query);
    const result = processNode(ast, unfilteredData);
    const filteredIds = result.map((result) => result["uid"]);

    dispatch({
      type: "UPDATE_FILTERED_IDS_BASED_ON_ADVANCED_SEARCH",
      payload: filteredIds,
    });
    dispatch({
      type: "UPDATE_FILTERED_DATA",
    });
    dispatch({
      type: "SET_PAGE_NUMBER",
      payload: 1,
    });
  };

  const displaySelectOperator = () => {
    return (
      <Fragment>
        <FormControl fullWidth size="small">
          <InputLabel>Operator</InputLabel>

          <Select
            value={operator}
            onChange={(e) => setOperator(e.target.value)}
            label="Operator"
            disabled={!query}
          >
            {operators.map((op) => (
              <MenuItem key={op} value={op}>
                {op}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </Fragment>
    );
  };

  const shouldDisplayAddToQueryButton = () => {
    if (field && term && operator) {
      return true;
    } else if (field && term && !query) {
      return true;
    } else {
      return false;
    }
  };

  return (
    <Stack spacing={1}>
      {" "}
      <Typography variant="h5" gutterBottom component="div">
        Advanced Search
      </Typography>
      <Typography variant="caption" gutterBottom component="div">
        Add terms to the query box
      </Typography>
      <FormControl fullWidth size="small">
        <InputLabel>Field</InputLabel>
        <Select
          value={field}
          onChange={(e) => setField(e.target.value)}
          label="Field"
        >
          {fields.map((field) => (
            <MenuItem key={field} value={field}>
              {fieldLabels[field]}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <TextField
        label="Enter a search term"
        value={term}
        onChange={(e) => setTerm(e.target.value)}
        fullWidth
        size="small"
      />
      {displaySelectOperator()}
      <Button
        variant="outlined"
        color="primary"
        size="small"
        onClick={handleAddToQuery}
        disabled={!shouldDisplayAddToQueryButton()}
      >
        Add to query
      </Button>
      <Typography variant="caption" gutterBottom component="div">
        Query Box
      </Typography>
      <TextField
        fullWidth
        multiline
        rows={2}
        variant="outlined"
        value={query}
        onChange={(e) => setQuery(e.target.value)}
        size="small"
        inputProps={{ style: { fontSize: 14 } }} // font size of input text
      />
      <Button
        variant="outlined"
        size="small"
        color="primary"
        style={{ marginTop: "10px" }}
        onClick={handleSearch}
      >
        Apply Query
      </Button>
      <Button variant="text" onClick={handleClick}>
        Overall Search
      </Button>
    </Stack>
  );
}

export default AdvancedSearch;
