import { rawData } from "../rawData";
import config from "../config.json";

const { dataSettings, tableSettings } = config;

const { uid } = dataSettings;
const getBasicMetadataFromIds = (arrayOfIds) => {
  const filteredData = rawData.filter((d) => arrayOfIds.includes(d[uid]));

  const selectedProperties = filteredData.map(
    ({
      doi,
      var_name,
      var_label,
      table_name,
      table_label,

      table_dir_name,

      table_doi,
      table_source,
      table_link_r,
      table_link_stata,
      table_link_sas,
      table_link_csv,
      table_link_python,
    }) => {
      return {
        table_dir_name,
        table_name,
        table_label,
        table_source,
        doi,
        table_doi,
        var_name,
        var_label,
        table_doi,
        table_link_r,
        table_link_stata,
        table_link_sas,
        table_link_csv,
        table_link_python,
      };
    }
  );

  return selectedProperties;
};

export { getBasicMetadataFromIds };
