export const sortData = (sortColumn, sortDescending, originalTableData) => {
  let sortDirectionDescending;

  // let counter = 0;

  if (sortDescending) {
    sortDirectionDescending = 1;
  } else {
    sortDirectionDescending = -1;
  }

  if (!sortColumn) {
    return originalTableData;
  } else {
    const sortedData = [...originalTableData].sort(compare);

    return sortedData;
  }

  function compare(a, b) {
    // counter += 1;

    let aSortColumn = a[sortColumn];
    let bSortColumn = b[sortColumn];

    if (aSortColumn) {
      try {
        aSortColumn = aSortColumn.toLowerCase();
      } catch (error) {
        console.error(error);
      }
    }

    if (bSortColumn) {
      try {
        bSortColumn = bSortColumn.toLowerCase();
      } catch (error) {
        console.error(error);
      }
    }

    if (aSortColumn < bSortColumn) {
      return 1 * sortDirectionDescending;
    }
    if (aSortColumn > bSortColumn) {
      return -1 * sortDirectionDescending;
    }
    return 0;
  }
};
