import { rawData } from "../rawData";
import config from "../config.json";

const { dataSettings } = config;
const { uid } = dataSettings;

const getDoisOfParentTablesForItemsInCart = (arrayOfIds) => {
  const filteredData = rawData.filter((d) => arrayOfIds.includes(d[uid]));
  const listOfTableDoisWithPossibleRepeats = filteredData.map(
    (d) => d.table_doi
  );
  const listOfTableDois = [];
  for (const tableDoi of listOfTableDoisWithPossibleRepeats) {
    if (!listOfTableDois.includes(tableDoi)) {
      listOfTableDois.push(tableDoi);
    }
  }
  return listOfTableDois;
};

export { getDoisOfParentTablesForItemsInCart };
