import { useEffect, useState, Fragment } from "react";

import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import Button from "@mui/material/Button";
import IconButton from "@mui/material/IconButton";
import { Grid } from "@mui/material";
import Stack from "@mui/material/Stack";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import DownloadForOfflineIcon from "@mui/icons-material/DownloadForOffline";
import Tooltip from "@mui/material/Tooltip";
import Link from "@mui/material/Link";
import DownloadIcon from "@mui/icons-material/Download";

import { useSelector } from "react-redux";

import { CSVLink } from "react-csv";

import { getAllMetadataFromIds } from "../../../Logic/getAllMetadataFromIds";
import { getBasicMetadataFromIds } from "../../../Logic/getBasicMetadataFromIds";
import { getLookupsForItemsInCart } from "../../../Logic/getLookupsForItemsInCart";
import { getNamesOfParentTablesForItemsInCart } from "../../../Logic/getNamesOfParentTablesForItemsInCart";
import { getDoisOfParentTablesForItemsInCart } from "../../../Logic/getDoisOfParentTablesForItemsInCart";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  pt: 2,
  px: 4,
  pb: 3,
};

function CartExportModal({ setOpenParentModal }) {
  const idsOfItemsInCart = useSelector((state) => state.idsOfItemsInCart);

  const [open, setOpen] = useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
    setOpenParentModal(false);
  };

  const [allMetadata, setAllMetadata] = useState([]);
  const [basicMetadata, setBasicMetadata] = useState([]);
  const [lookups, setLookups] = useState([]);
  const [namesOfParentTables, setNamesOfParentTables] = useState([]);
  const [parentTableMetadata, setParentTableMetadata] = useState([]);
  const [linkToScriptExists, setLinkToScriptExists] = useState(false);
  const [linkToDataExists, setLinkToDataExists] = useState(false);

  useEffect(() => {
    const arrayOfIds = idsOfItemsInCart;

    const allMetadata = getAllMetadataFromIds(arrayOfIds);
    setAllMetadata(allMetadata);

    const basicMetadata = getBasicMetadataFromIds(arrayOfIds);

    const selectedBasicMetadata = basicMetadata.map(
      ({
        doi,
        var_name,
        var_label,
        table_label,
        table_name,
        table_source,
        table_dir_name,
      }) => {
        return {
          doi,
          var_name,
          var_label,
          table_label,
          table_name,
          table_source,
          table_dir_name,
        };
      }
    );
    setBasicMetadata(selectedBasicMetadata);

    console.log({ arrayOfIds });

    const lookups = getLookupsForItemsInCart(arrayOfIds);

    console.log({ lookups });

    setLookups(lookups);

    const namesOfParentTables =
      getNamesOfParentTablesForItemsInCart(arrayOfIds);
    setNamesOfParentTables(namesOfParentTables);

    // Get dois of parent tables
    const doisOfParentTables = getDoisOfParentTablesForItemsInCart(arrayOfIds);

    // Get all records of variable level metadata for variables belonging to those tables
    const filteredBasicMetadata = basicMetadata.filter((d) =>
      doisOfParentTables.includes(d.table_doi)
    );

    // isolate the table level metadata for those records
    const selectedProperties = filteredBasicMetadata.map(
      ({
        table_doi,
        table_source,
        table_name,
        table_link_r,
        table_link_stata,
        table_link_sas,
        table_link_csv,
        table_link_python,
      }) => {
        return {
          table_doi,
          table_source,
          table_name,
          table_link_r,
          table_link_stata,
          table_link_sas,
          table_link_csv,
          table_link_python,
        };
      }
    );

    // Get unique objects based on the "table_doi" property
    const tableLevelMetadata = selectedProperties.filter((obj, index, self) => {
      return index === self.findIndex((o) => o.table_doi === obj.table_doi);
    });

    setParentTableMetadata(tableLevelMetadata);

    /*

    // Check if any object has the key "table_link_csv"
    const linkToDataExists = tableLevelMetadata.some((obj) =>
      obj.hasOwnProperty("table_link_csv")
    );
    */
    let linkToDataExists = false;
    let linkToScriptExists = false;
    for (const datum of tableLevelMetadata) {
      if (datum["table_link_csv"]) {
        linkToDataExists = true;
      }

      if (
        datum["table_link_stata"] ||
        datum["table_link_sas"] ||
        datum["table_link_stata"]
      ) {
        linkToScriptExists = true;
      }
    }

    setLinkToDataExists(linkToDataExists);
    setLinkToScriptExists(linkToScriptExists);
  }, [idsOfItemsInCart]);

  const renderButtonToDownloadLookups = () => {
    return (
      <CSVLink data={lookups} filename={"lookups.csv"}>
        <Tooltip
          title="Lookups include: 
                    Name of Parent Table, Variable DOI, Variable Name,
                    Permissible Value, and Permissible Value Meaning"
        >
          <Button size="small">Download Lookups</Button>
        </Tooltip>
      </CSVLink>
    );
  };

  const renderWhenNoLookups = () => {
    return (
      <Button size="small" disabled>
        No Lookups for variables in Cart
      </Button>
    );
  };

  const renderTableRows = () => {
    return parentTableMetadata.map((metadatum, i) => {
      const {
        table_doi,
        table_link_csv,
        table_link_r,
        table_link_sas,
        table_link_stata,
        table_link_python,
        table_name,
        table_source,
      } = metadatum;
      const key = table_doi;

      let nameOfDataFile,
        nameOfRScript,
        nameOfSasScript,
        nameOfStataScript,
        nameOfPythonScript;

      if (table_link_csv) {
        nameOfDataFile = table_link_csv.substring(
          table_link_csv.lastIndexOf("/") + 1
        );
      }

      if (table_link_r) {
        nameOfRScript = table_link_r.substring(
          table_link_r.lastIndexOf("/") + 1
        );
      }

      if (table_link_sas) {
        nameOfSasScript = table_link_sas.substring(
          table_link_sas.lastIndexOf("/") + 1
        );
      }

      if (table_link_stata) {
        nameOfStataScript = table_link_stata.substring(
          table_link_stata.lastIndexOf("/") + 1
        );
      }

      if (table_link_python) {
        nameOfPythonScript = table_link_python.substring(
          table_link_python.lastIndexOf("/") + 1
        );
      }

      const renderTableLink = (table_link_csv, nameOfDataFile) => {
        if (table_link_csv) {
          return (
            <TableCell>
              <a href={`${table_link_csv}`} download={`${nameOfDataFile}`}>
                {nameOfDataFile}
              </a>
            </TableCell>
          );
        } else {
          return <TableCell>{table_name}</TableCell>;
        }
      };

      const renderLinkToScript = (fileName, linkToFile) => {
        return (
          <Button
            variant="text"
            color="primary"
            size="small"
            startIcon={<DownloadIcon />}
            href={linkToFile}
            download={fileName}
          ></Button>
        );
      };

      return (
        <TableRow key={key}>
          <TableCell>{table_source}</TableCell>
          {renderTableLink(table_link_csv, nameOfDataFile)}
          <TableCell align="center">
            {table_link_r
              ? renderLinkToScript(nameOfRScript, table_link_r)
              : null}
          </TableCell>
          <TableCell>
            {table_link_sas
              ? renderLinkToScript(nameOfSasScript, table_link_sas)
              : null}
          </TableCell>
          <TableCell>
            {table_link_stata
              ? renderLinkToScript(nameOfStataScript, table_link_stata)
              : null}
          </TableCell>
          <TableCell>
            {table_link_python
              ? renderLinkToScript(nameOfPythonScript, table_link_python)
              : null}
          </TableCell>
        </TableRow>
      );
    });
  };

  const renderWhenDataOrScriptsExist = () => {
    return (
      <Grid item xs={12}>
        <Card sx={{ minWidth: 275 }}>
          <CardContent>
            <Typography variant="h5" color="text.secondary" gutterBottom>
              Data and Scripts
            </Typography>

            <Stack>
              <Typography variant="body2" sx={{ mb: 1.5 }}>
                <b>Data</b>. Download the parent CSV files for the variables in
                your cart.{" "}
                <i>
                  Permission to access the{" "}
                  <Link href={"https://nih.sharepoint.com/sites/NIA-BLSA"}>
                    BLSA Data Science SharePoint Online site
                  </Link>{" "}
                  is required.
                </i>
              </Typography>

              <Typography variant="body2" sx={{ mb: 1.5 }}>
                <b>Scripts</b>. Download scripts that will load your CSV files
                into R, SAS, or Stata and then label the variables and values.
              </Typography>

              <TableContainer component={Paper} sx={{ maxHeight: 300 }}>
                <Table size="small" stickyHeader aria-label="a dense table">
                  <TableHead>
                    <TableRow>
                      <TableCell>Table Source </TableCell>
                      <TableCell>Parent Table</TableCell>
                      <TableCell align="center">R Script</TableCell>
                      <TableCell>SAS Script</TableCell>
                      <TableCell>Stata Script</TableCell>
                      <TableCell>Python Script</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>{renderTableRows()}</TableBody>
                </Table>
              </TableContainer>
            </Stack>
          </CardContent>
        </Card>
      </Grid>
    );
  };

  const renderWhenNeitherDataNorScriptsExist = () => {
    return (
      <Grid item xs={12}>
        <Card sx={{ minWidth: 275 }}>
          <CardContent>
            <Typography variant="h5" color="text.secondary" gutterBottom>
              Data and Scripts
            </Typography>

            <Stack>
              <Typography variant="body2" sx={{ mb: 1.5 }}>
                The scripts and data for the variables you have selected are not
                available at the moment.
              </Typography>
            </Stack>
          </CardContent>
        </Card>
      </Grid>
    );
  };

  return (
    <Fragment>
      <Tooltip title="Download files related to cart contents">
        <IconButton onClick={handleOpen}>
          <DownloadForOfflineIcon></DownloadForOfflineIcon>
        </IconButton>
      </Tooltip>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="child-modal-title"
        aria-describedby="child-modal-description"
      >
        <Box sx={{ ...style }}>
          <h2 id="child-modal-title">
            Download files related to cart contents
          </h2>

          <Grid container spacing={1}>
            <Grid item xs={6}>
              <Card>
                <CardContent>
                  <Typography variant="h5" color="text.secondary" gutterBottom>
                    Metadata
                  </Typography>

                  <Typography
                    sx={{ mb: 1.5 }}
                    color="text.secondary"
                  ></Typography>
                  <Typography variant="body2">
                    Download a CSV file of metadata for the variables in your
                    cart.
                  </Typography>
                </CardContent>
                <CardActions>
                  <CSVLink data={basicMetadata} filename={"basic-metadata.csv"}>
                    <Tooltip title="Basic metadata includes Variable DOI, Variable Name,  Name of Parent Table, Label of Parent Table, Table Source, and Directory of Parent Table.">
                      <Button size="small">Basic</Button>
                    </Tooltip>
                  </CSVLink>
                  <CSVLink
                    data={allMetadata}
                    filename={"detailed-metadata.csv"}
                  >
                    <Tooltip title="Detailed metadata includes the Basic metadata plus everything else in the MetaEncyclopedia">
                      <Button size="small">Detailed</Button>
                    </Tooltip>
                  </CSVLink>
                </CardActions>
              </Card>
            </Grid>
            <Grid item xs={6}>
              <Card>
                <CardContent>
                  <Typography variant="h5" color="text.secondary" gutterBottom>
                    Lookups
                  </Typography>

                  <Typography
                    sx={{ mb: 1.5 }}
                    color="text.secondary"
                  ></Typography>
                  <Typography variant="body2">
                    Download a CSV file of lookups for list-type variables in
                    your cart (Permissible Values and Meanings).
                  </Typography>
                </CardContent>
                <CardActions>
                  {lookups.length > 0
                    ? renderButtonToDownloadLookups()
                    : renderWhenNoLookups()}
                </CardActions>
              </Card>
            </Grid>

            {linkToScriptExists || linkToDataExists
              ? renderWhenDataOrScriptsExist()
              : renderWhenNeitherDataNorScriptsExist()}
          </Grid>

          <Button onClick={handleClose}>Exit</Button>
        </Box>
      </Modal>
    </Fragment>
  );
}

export default CartExportModal;
