import React, { useState } from "react";

import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";

import InfoIcon from "@mui/icons-material/Info";
import IconButton from "@mui/material/IconButton";

import TrashRow from "./TrashRow";
import TrashAll from "./TrashAll";

import config from "../../../config.json";

import AdditionalInfo from "../../TableWithPaginator/TableDisplay/AdditionalInfo";
const { tableSettings } = config;

const { columnsOnAdditionalInfo } = tableSettings;

export default function DenseTable({
  cartData,
  uid,
  cartTableSettingsWithLabels,
}) {
  // State for additional info modal
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => setOpen(false);

  // useState to keep track of which datum
  // should appear on the additional Information modal
  const [selectedAdditionalInfoDatum, setSelectedAdditionalInfoDatum] =
    useState(null);

  return (
    <TableContainer component={Paper}>
      <Table size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            {cartTableSettingsWithLabels.map((datum, i) => (
              <TableCell key={i}>{datum["field_label"]}</TableCell>
            ))}
            <TableCell>Additional Info</TableCell>
            <TableCell>
              <TrashAll></TrashAll>
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {cartData.map((datum, j) => {
            const rowKey = `${datum[uid]}-cart-row-${j}`;
            return (
              <TableRow
                key={rowKey}
                sx={{
                  "&:last-child td, &:last-child th": { border: 0 },
                  "&:nth-of-type(odd)": {
                    backgroundColor: "#f5f5f5",
                  },
                }}
              >
                {cartTableSettingsWithLabels.map((cartCol, i) => {
                  const column = cartCol["field_name"];
                  const columnKey = `${datum[uid]}-cart-col-${i}row-${j}`;
                  return <TableCell key={columnKey}>{datum[column]}</TableCell>;
                })}
                <TableCell>
                  <IconButton
                    aria-label="delete"
                    onClick={() => {
                      setSelectedAdditionalInfoDatum(datum);
                      handleOpen();
                    }}
                    size="small"
                  >
                    <InfoIcon fontSize="inherit" sx={{ color: "#999" }} />
                  </IconButton>
                </TableCell>

                <TableCell>
                  <TrashRow uid={datum[uid]}></TrashRow>
                </TableCell>
              </TableRow>
            );
          })}
        </TableBody>
      </Table>
      <AdditionalInfo
        open={open}
        handleClose={handleClose}
        columnsOnAdditionalInfo={columnsOnAdditionalInfo}
        datum={selectedAdditionalInfoDatum}
      ></AdditionalInfo>
    </TableContainer>
  );
}
