import React from "react";

import Card from "@mui/material/Card";

import CardContent from "@mui/material/CardContent";
import { Grid, Typography } from "@mui/material";
import { Tooltip } from "@mui/material";
import InfoIcon from "@mui/icons-material/Info";
import IconButton from "@mui/material/IconButton";
import { Stack } from "@mui/material";

const DateDisplay = (link_to_image, table_name, table_source) => {
  let tooltipText = "";

  if (table_source === "CRBShare") {
    tooltipText = `The image above was produced using the Visit Date (dov) column from the ${table_name} table.`;
  }
  if (table_source === "OpenClinica") {
    tooltipText = `To produce the image above, Observation Dates (Obsdates) from the ${table_name} table were compared with Visit Dates from the Clean
    Cohort File. If an Obsdate fell outside of a six-month window of the
    Visit Date, then the Obsdate was excluded from the visualization.`;
  }

  return (
    <Card>
      <CardContent>
        <Stack>
          <Grid
            container
            sx={{
              display: "flex",
              justifyContent: "center",
              alignContent: "center",
              alignItems: "center",
              marginTop: 0.42,
            }}
          >
            <img src={link_to_image} width="100%"></img>
          </Grid>

          <Tooltip title={tooltipText}>
            <IconButton color="neutral">
              <InfoIcon></InfoIcon>
            </IconButton>
          </Tooltip>
        </Stack>
      </CardContent>
    </Card>
  );
};

export default DateDisplay;
