import React, { useState } from "react";

import { Stack } from "@mui/material";
import Tooltip from "@mui/material/Tooltip";
import Typography from "@mui/material/Typography";

import SortToggle from "./SortToggle";

function TableHeadCell({
  columnName,
  columnDisplayName,
  sortColumn,
  sortDescending,
  setSortColumn,
  setSortDescending,
}) {
  const [hovering, setHovering] = useState(false);

  const onMouseEnter = () => {
    setHovering(true);
  };

  const onMouseLeave = () => {
    setHovering(false);
  };

  const isCurrentSortColumn = columnName === sortColumn;

  const renderPriorityHeader = () => {
    return (
      <Stack
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
        direction="row"
        alignItems="center"
      >
        <Tooltip
          title="Priority: Recommended usability level of the variable; Core = highest priority"
          placement="top"
        >
          <Typography>{columnDisplayName}</Typography>
        </Tooltip>
        <SortToggle
          hovering={hovering}
          isCurrentSortColumn={isCurrentSortColumn}
          sortDescending={sortDescending}
          setSortColumn={setSortColumn}
          setSortDescending={setSortDescending}
          columnName={columnName}
        ></SortToggle>
      </Stack>
    );
  };

  if (columnDisplayName === "Priority") {
    return renderPriorityHeader();
  } else {
    return (
      <Stack
        onMouseEnter={onMouseEnter}
        onMouseLeave={onMouseLeave}
        direction="row"
        alignItems="center"
      >
        <Typography>{columnDisplayName}</Typography>

        <SortToggle
          hovering={hovering}
          isCurrentSortColumn={isCurrentSortColumn}
          sortDescending={sortDescending}
          setSortColumn={setSortColumn}
          setSortDescending={setSortDescending}
          columnName={columnName}
        ></SortToggle>
      </Stack>
    );
  }
}

export default TableHeadCell;
